.collection-item {
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;
  min-width: 22%;
  margin: 10px;
  flex: 1 1 220px;

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }

  .collection-footer {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;

    .name {
      margin-bottom: 15px;
    }

    // .price {
    // }
  }

  @media all and (min-width: 768px) {
  }
}
